<template>
  <div>
    <bar/>
  </div>
  <div id="app">
    <h2>ChiP-Seq</h2>
    <div class="container">
      <!--  -->
      <div class="column">
        <h3></h3>
        <upload/>
      </div>
      <!--  -->
      <div class="column">
        <h3></h3>
        <draggable
          :list="availableModules"
          group="modules"
          item-key="id"
          @change="logChange"
          class="drag-area"
        >
          <template #item="{ element }">
            <div class="module-card">
              <div class="module-header">
                <span>{{ element.name }}</span>
              </div>
              <div class="module-description">{{ element.description }}</div>
            </div>
          </template>
        </draggable>
      </div>

      <!--  -->
      <div class="column">
        <h3></h3>
        <draggable
          :list="selectedModules"
          group="modules"
          item-key="id"
          @change="logChange"
          :move="checkMove"
          class="drag-area"
        >
          <template #item="{ element }">
            <div class="module-card">
              <div class="module-header">
                <span>{{ element.name }}</span>
              </div>
              <div class="module-description">{{ element.description }}</div>

              <!--  hasParams  true  -->
              <div v-if="element.hasParams">
                <div v-if="element.showParams" class="module-params">
                  <div v-for="param in element.parameters" :key="param.name" class="param-item">
                    <label :for="`${element.id}-${param.name}`">{{ param.label }}</label>

                    <!--  -->
                    <input
                      v-if="param.type === 'text' || param.type === 'number' || param.type === 'password' || param.type === 'email'"
                      :type="param.type"
                      :id="`${element.id}-${param.name}`"
                      v-model="param.value"
                      :placeholder="param.placeholder"
                    />

                    <!--  -->
                    <select
                      v-else-if="param.type === 'select'"
                      :id="`${element.id}-${param.name}`"
                      v-model="param.value"
                    >
                      <option v-for="option in param.options" :key="option.value" :value="option.value">
                        {{ option.label }}
                      </option>
                    </select>

                    <!--  -->
                    <div v-else-if="param.type === 'radio'" class="radio-group">
                      <label v-for="option in param.options" :key="option.value">
                        <input
                          type="radio"
                          :name="`${element.id}-${param.name}`"
                          :value="option.value"
                          v-model="param.value"
                        />
                        {{ option.label }}
                      </label>
                    </div>

                    <!--  -->
                    <input
                      v-else-if="param.type === 'checkbox'"
                      type="checkbox"
                      :id="`${element.id}-${param.name}`"
                      v-model="param.value"
                    />

                    <!--  -->
                    <input
                      v-else-if="param.type === 'file'"
                      type="file"
                      :id="`${element.id}-${param.name}`"
                      @change="handleFileUpload($event, param)"
                    />
                    
                  </div>
                </div>
                <button @click="toggleParams(element)">
                  {{ element.showParams ? '' : '' }}
                </button>
              </div>
            </div>
          </template>
        </draggable>
      </div>
    </div>
    
    <div class="submit">
      <button @click="submitPipeline"></button>
    </div>
    
  </div>
</template>

<script>
import bar from '@/components/Tool_Navigation.vue';
import draggable from "vuedraggable";
import { ref } from "vue";
import upload from '@/components/FileCheck.vue';
export default {
  name: 'apparent',
  components:{
    bar,
    draggable,
    upload,
  },
  setup() {
    // 
    const availableModules = ref([
      
      {
        id: 1,
        name: "FastQC",
        description: "",
        hasParams: false,//
        isRequired: false,//
      },
    ]);

    // 
    const selectedModules = ref([
      {
        id: 1,
        name: "fastq-dump",
        description: "srafastq",
        hasParams: true,
        isRequired: true,
        showParams: false,
        parameters:[
        { name: "singleORpaired", label: "", type: "select", value: "", options: [
            { value: "single", label: "" },
            { value: "paired", label: "" },
          ],
        },]
      },
      {
        id:2,
        name:"Fastp",
        description:"",
        hasParams: false,
        isRequired: true,
      },
      {
        id: 3,
        name: "Bowtie2",
        description: "",
        hasParams: true,
        isRequired: true,
        showParams: false,
        parameters: [
          { name: "singleORpaired", label: "", type: "select", value: "", options: [
            { value: "single", label: "" },
            { value: "paired", label: "" },
          ], },

        ],
      },
      {
        id: 4,
        name: "markdup",
        description: "PCR",
        hasParams: false,
        isRequired: true,
      },
      {
        id: 5,
        name: "bamCoverage",
        description: "Bambigwig",
        hasParams: true,
        isRequired: true,
        showParams: false,
        parameters: [
          { name: "binSize", label: "bin", type: "number", value: "", placeholder: "bin" },
          { name: "normalizeUsing", label: "RPKMRPMBED", type: "text", value: "", placeholder: "RPKMRPMBED" },
        ],
      },
      {
        id: 6,
        name: "macs2",
        description: "peak calling",
        hasParams: false,
        isRequired: true,
        showParams: false,
        parameters: [
          {
            name: "fileAssignments",
            label: "",
            type: "nested",
            value: [], // 
            options: [], // 
          },
        ],
      },
    ]);
    const handleFilesUploaded = (files) => {
      const fileOptions = files.map((file) => ({
        value: file.name,
        label: file.name,
      }));

      //  macs2  parameters
      const macs2Module = selectedModules.value.find(
        (module) => module.name === "macs2"
      );
      if (macs2Module) {
        const fileAssignments = files.map((file) => ({
          fileName: file.name,
          role: "", // 
          controlFile: "", // 
        }));
        macs2Module.parameters[0].value = fileAssignments;
        macs2Module.parameters[0].options = fileOptions;
      }
    };
    // 
    const toggleParams = (module) => {
      module.showParams = !module.showParams;
    };

    //  pipeline
    const submitPipeline = () => {
      const pipeline = selectedModules.value.map((module) => ({
        moduleName: module.name,
        parameters: module.parameters.reduce((acc, param) => {
          acc[param.name] = param.value;
          return acc;
        }, {}),
      }));
      console.log(" pipeline:", pipeline);
      //  API  pipeline 
    };

    // 
    const logChange = (event) => {
      console.log(":", event);
    };

    const checkMove = (event) => {
      const { draggedContext, relatedContext } = event;
      const draggedElement = draggedContext.element;
      const isDraggingToLeft = relatedContext.list === availableModules.value;

      // 
      if (draggedElement.isRequired && isDraggingToLeft) {
        return false;
      }
      return true;
    };

    return {
      availableModules,
      selectedModules,
      toggleParams,
      submitPipeline,
      logChange,
      checkMove,
      handleFilesUploaded,
    };
  },
};
</script>

<style>
#app {
  font-family: Arial, sans-serif;
  padding-top: 20px;
}

.container {
  display: flex;
  gap: 20px;
  height: 400px;
}

.column {
  flex: 1;
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 5px;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.column h3 {
  margin: 0 0 10px 0;
}

.drag-area {
  flex: 1;
  overflow-y: auto;
  padding-right: 10px;
}

.module-card {
  border: 1px solid #ddd;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  background-color: #f9f9f9;
  cursor: move;
}

.module-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.module-description {
  font-size: 12px;
  color: #666;
}

.module-params {
  margin-top: 10px;
}

.param-item {
  margin-bottom: 10px;
}

.param-item label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.param-item input {
  width: 100%;
  padding: 5px;
  box-sizing: border-box;
}

button {
  margin-top: 10px;
  padding: 5px 10px;
  cursor: pointer;
}

.submit{
  text-align: right;
  margin: 10px;
}
</style>